import React, { useEffect, useState } from 'react'
import css from "../css/filebox.module.css"
import ModalWindow from './ModalWindow';
import { fetchFile } from '../util/Fetch';

const FileBox = ({ url, data, removeFile }) => {

  const name = data.name;
  const date = data.date;
  const day = String(date).split("T")[0]
  const time = String(date).split("T")[1].split(".000Z")[0];
  const mime = String(data.mimetype).includes("video") ? "ss" : "img";
  const path = mime === "ss" ? data.sspath : data.filepath
  const f = url + "/" + mime + "/" + path;
  const v = url + "/video/" + data.filepath;
  const [modal, setModal] = useState(false)
  const [file, setFile] = useState();
  const [video, setVideo] = useState();

  useEffect(()=>{
    if(file === undefined){
      fetchFile(f, "GET").then(res=> setFile(res))
    }
    if(video === undefined && mime === "ss"){
      fetchFile(v, "GET").then(res=> setVideo(res))
    }
  },[])
  if(file === undefined)return;
  return (
    <>
      {modal ? <ModalWindow url={mime === "ss"? v : f} file={data} setModal={setModal} /> : <></>}
      <div className={css.Box}>
        {removeFile !== undefined ? <div className={css.Delete} onClick={() => removeFile(data)}></div> : <></>}
        <div className={css.Image} onClick={() => setModal(true)}>
          <img src={f} alt="" />
        </div>
        <div style={{ marginTop: "-20px", marginBottom: "20px", fontSize: "22px" }}>{mime === "ss" ? "" : ""}</div>
        <div> : {name}</div>
        <div> : {day + " " + time}</div>
      </div>
    </>
  )
}

export default FileBox