import React from 'react'

const Progress = ({ width, total, free, usage }) => {


    const progressPer = () => {
        let t = Math.round(total);
        let u = Math.round(usage);
        return (u / t) * 100;
    }
    return (
        <div style={{
            width: width,
            position: "relative",
            height: "13px",
            borderRadius: "20px",
            boxShadow: "0px 0px 5px -1.5px #777777",
            backgroundColor: "white",
            padding: "1.5px"
        }}>
            <div style={{
                position: "absolute",
                top: "50%",
                left: "50%",
                transform: "translate(-50%, -50%)",
                fontSize: "10px",
            }}>
                {Math.round(progressPer())}% 
            </div>
            <div style={{
                width: width,
                position: "absolute",
                top: "-25px",
                fontSize: "15px"
            }}>
                {usage}GB / {total}GB
            </div>
            <div style={{
                width: Math.round(progressPer()) + "%",
                height: "13px",
                borderRadius: progressPer() === 100 ? "20px" : "20px 5px 5px 20px",
                display: "flex",
                backgroundImage: progressPer() >= 80 ? "red" : "linear-gradient(90deg, rgba(144, 245, 154, 1), rgba(4, 202, 255, 1))",
            }}>
            </div>
        </div>
    )
}

export default Progress