import React from 'react'
import css from "../css/header.module.css";
import StorageProgress from "../util/StorageProgress";
import { useNavigate } from 'react-router-dom';

const Header = ({ name, removeCookie, storage }) => {
    const navigate = useNavigate();
    const handleClick = (param) => {
        navigate("/Monooki"+param)
    }
    return (
        <>
            <div className={css.Field}>
                <div className={css.Logo} onClick={()=> handleClick("/")}></div>
                <div className={css.Menu}>
                    <div className={css.Name}>{name} </div>
                    <div>
                        <StorageProgress width={"200px"} total={storage.total} free={storage.free} usage={storage.usage} />
                    </div>
                    <div className={css.Link} onClick={()=> handleClick("/posts")}></div>
                    <div className={css.Link} onClick={()=> handleClick("/uplode")}></div>
                    <div className={css.Link} onClick={() => removeCookie("data")}></div>
                </div>
            </div>
            <br />
            <br />
            <br />
            <br />
            <br />
            <br />
            <br />
            <br />
        </>

    )
}

export default Header