import React from 'react'
import FileBox from './FileBox'
import { fetchData } from '../util/Fetch'
const PostList = ({ id, url, files, setFiles }) => {

    const removeFile = (file) => {
        setFiles(files.filter(f => f !== file))
        const deleteParam = String(file.mimetype).includes("video") ? "deletevideo" : "deleteimg";
        fetchData(url + "/"+deleteParam + "/" + file.filepath, "DELETE").then(res => res.error === undefined ? alert(res.success) : alert(res.error));
    }
    return (
        <div style={{ display: 'flex', flexWrap: "wrap", margin: "0 auto", width: "99%" }}>
            {files.filter(f => f.id === id).map(f => (
                <div key={f.filepath} >
                    <FileBox url={url} data={f} removeFile={removeFile}/>
                </div>
            ))}
        </div>
    )
}

export default PostList