import React from 'react'
import { useForm } from 'react-hook-form';
import { login } from '../util/LoginFetch';
import css from "../css/login.module.css";

const LoginPage = ({url,  cookie, setCookie }) => {

    const { register, handleSubmit, reset, getValues } = useForm();

    const onSubmit = (v) => {

        let d = login(url, v.id, v.pass);
        d.then(res => {
            if (res.id !== undefined) {
                setCookie("data", { "id": res.id, "name": res.name });
            } else {
                alert(res.error)
            }
        })
        reset();
    }
    return (
        <div className={css.Field}>

            <form onSubmit={handleSubmit(onSubmit)} className={css.Form}>
                <h1 className={css.Logo}>LoginPage</h1>
                <label>
                    <input type="id" {...register("id")} placeholder=' ID' className={css.Input}/>
                </label>
                <label>
                    <input type="password" {...register("pass")} placeholder=' Password'  className={css.Input}/>
                </label>
                <input type="submit" value={""} className={css.Button}/>
            </form>
        </div>

    )
}

export default LoginPage