import './App.css';
import { useEffect, useState } from 'react';
import { useNgrokUrl } from './util/NgrokUrl';
import LoginPage from './component/LoginPage';
import { useCookies } from "react-cookie";
import Header from './component/Header';
import { fetchData } from './util/Fetch';
import { Route, Routes } from 'react-router-dom';
import TopPage from './component/TopPage';
import PostList from './component/PostList';
import Uplode from "./component/Uplode";


function App() {

  const [storage, setStorage] = useState();
  const [files, setFiles] = useState([]);
  const [filter, setFilter] = useState("all");
  const { data } = useNgrokUrl();
  const url = "https://ahaha.info:30030";
  const [cookies, setCookie, removeCookie] = useCookies(["data"]);

  const getStorageNum = () => {
    let total = storage.total;
    let free = storage.free;
    let usage = total - free;
    return {
      total: Math.round((total / 1e+9) * 100) / 100,
      free: Math.round((free / 1e+9) * 100) / 100,
      usage: Math.round((usage / 1e+9) * 100) / 100
    };
  }

  useEffect(() => {
    if (storage === undefined) {
      fetchData(url + "/disk", "GET").then(res => {
        setStorage(res)
      });
      fetchData(url + "/get", "GET").then(res => {
        setFiles(res)
      });
    }
  }, [data])
  if (storage === undefined) return <dir>Loading...</dir>
  if (cookies.data === undefined) return <div><LoginPage url={url} cookie={cookies} setCookie={setCookie} /></div>
  return (
    <div className="App">
      <Header name={cookies.data.name} removeCookie={removeCookie} storage={getStorageNum()} />
      <Routes>
        <Route path='/Monooki' element={<TopPage filter={filter} setFilter={setFilter} files={files} url={url}/>}/>
        <Route path='/Monooki/posts' element={<PostList id={cookies.data.id} url={url} files={files} setFiles={setFiles}/>}/>
        <Route path='/Monooki/uplode' element={<Uplode id={cookies.data.id} url={url}/>}/>
      </Routes>
    </div>
  );
}

export default App;
