import React from 'react'
import css from "../css/modal.module.css"
import "../../node_modules/video-react/dist/video-react.css";
import { Player } from 'video-react';

const ModalWindow = ({ url, file, setModal ,video}) => {
    const mime = String(file.mimetype).includes("video") ? "video" : "img";
    const day = String(file.date).split("T")[0]
    const time = String(file.date).split("T")[1].split(".000Z")[0];
    return (
        <div className={css.ModalField}>
            <div className={css.File}>
                <div className={css.Close} onClick={() => setModal(false)}></div>
                {mime === "video" ?
                    <Player playsInline src={url}/>
                     :
                    <img src={url} />
                }
                <div className={css.Content}> : {file.name}</div>
                <div className={css.Content}> : {day+" "+time}</div>
                <div className={css.Content}><a href={url} download></a></div>
            </div>
        </div>
    )
}

export default ModalWindow